	module.exports = {"data":{"markdownRemark":{"html":"<p>Mid-Autumn Festival, the moonlight is pleasant. In the middle of the night, walking outside, the night wind hit, quite cool. The bright moonlight, sprinkled on the ground, a silver, makes people think.</p>\n<p>Suddenly I thought of a French novel, \"Moonlight,\" which said that a priest could never understand the behavior of his niece, nor did he understand why young people should talk about love and not wholeheartedly dedicate themselves to God. One night, he found that the prostitute was meeting with a young man again, and was furious and rushed out of the church. Suddenly, he stopped. He found that the moonlight was so chaste, so beautiful, and he had a problem in his heart. \"If the things that young people do are sin, God is not allowed, why is the moonlight tonight so bright?\" Why did God bring such a bright moonlight tonight?\" So he realized that it was not sin, but a good thing. He went back and fell asleep quietly.</p>\n<p>Nature is sometimes inspiring, and often the most profound inspiration. People live in nature, and when they are enlightened, they have enlightenment and no awareness. There is no inspiration. It is not an easy task to be enlightened in nature. Most people live in nature, without consciousness, but destroy nature. When nature reverts to the contrary, it is natural to know that this is the most pitiful. The education obtained from the natural grass and trees is not so much external as it is internal, so it is difficult.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0146.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0146.md","frontmatter":{"title":"September 20th Tuesday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0146.md absPath of file >>> MarkdownRemark"}}

